import { ComponentProps } from "react";

export type IconName =
  | "add"
  | "arrow-right-up"
  | "menu"
  | "close"
  | "arrow-down-s"
  | "chevron"
  | "triangle"
  | "clock"
  | "quote"
  | "arrow-right-line"
  | "arrow-drop-down-line"
  | "expand-right-line";

interface Props extends ComponentProps<"svg"> {
  icon: IconName;
  size?: number;
}

export default function Icon({ icon, size = 24, ...props }: Props) {
  return (
    <svg height={size} width={size} {...props}>
      <use xlinkHref={`/sprite.svg#${icon}`} />
    </svg>
  );
}
