import s from "./BackgroundImage.module.scss";
import cx from "clsx";

interface Props {
  position?: "absolute" | "fixed";
}

export default function BackgroundImage({ position = "fixed" }: Props) {
  return (
    <div className={cx(s.background, s[position])}>
      <img
        src="/natf-background-image.webp"
        alt=""
        role="presentation"
        className={s.background__img}
      />
    </div>
  );
}
