"use client";

import { useEffect, useState } from "react";
import Button from "../Button/Button";
import LinkButton from "../LinkButton/LinkButton";
import styles from "./IntroPopup.module.scss";
import clsx from "clsx";
import Icon from "../Icon/Icon";

export default function IntroPopup() {
  const [popupClosed, setPopupClosed] = useState(false);
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    // Check if the popup has already been shown in this session
    const popupShown = sessionStorage.getItem("introPopupShown");

    if (!popupShown) {
      // Show the popup and set the flag in session storage
      setIsVisible(true);
      sessionStorage.setItem("introPopupShown", "true");
    }
  }, []);

  if (!isVisible) {
    return null; // Don't render the popup if it shouldn't be visible
  }

  return (
    <div className={clsx(styles.popup, popupClosed && styles.popup__closed)}>
      <div
        onClick={() => {
          setPopupClosed(true);
        }}
        className={styles.popup__backdrop}
      ></div>
      <div className={styles.popup__inner}>
        <div className={styles.popup__content}>
          <div className={styles.popup__header}>
            <img
              className={styles.popup__logo}
              src="vasculearn-logo-purple.svg"
              alt="Logo Vasculearn network"
            />
            <button onClick={() => setPopupClosed(true)}>
              <Icon icon="close" />
            </button>
          </div>
          <div className={styles.popup__text}>
            <p className="body-l">
              With this toolkit, you will discover essential topics to discuss
              with your patients who have VTE and receive guidance on the
              optimal timing for these conversations. You will also find a list
              of relevant patient questions to explore and best practices for
              enhancing your communication skills and fostering better patient
              engagement.
            </p>
          </div>
        </div>
        <div className={styles.popup__buttons}>
          <Button
            label="Get started"
            rank="primary"
            onClick={(event: React.MouseEvent<HTMLButtonElement>) => {
              event.preventDefault();
              setPopupClosed(true);
            }}
          />
          <LinkButton
            href="https://thrombosis.org/providers/vte-communication-guide/"
            target="_blank"
            label="About this project"
            rank="tertiary"
            icon={true}
          />
        </div>
      </div>
    </div>
  );
}
