import { ComponentProps } from "react";
import Icon from "../Icon/Icon";
import styles from "./LinkButton.module.scss";

interface Props extends ComponentProps<"a"> {
  label: string;
  secondLabel?: string;
  icon?: boolean;
  href: string;
  target?: string;
  rank?: "primary" | "secondary" | "tertiary";
  active?: boolean;
}

export default function LinkButton({
  label,
  secondLabel,
  icon,
  href,
  target = "_self",
  rank = "primary",
  active = false,
  ...props
}: Props) {
  return (
    <a
      href={href}
      target={target}
      className={`${styles.link} ${styles[rank]} ${
        styles[active ? "active" : ""]
      }`}
      {...props}
    >
      {label}
      {secondLabel ? <span>{secondLabel}</span> : null}
      {icon ? <Icon icon="arrow-right-up" size={16} /> : null}
    </a>
  );
}
