"use client";

import { useEffect, useState } from "react";
import s from "./Menu.module.scss";
import LinkButton from "../LinkButton/LinkButton";
import BackgroundImage from "../BackgroundImage/BackgroundImage";

export default function Menu() {
  const [scrolled, setScrolled] = useState(false);
  const [url, setUrl] = useState("");

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 10) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    setUrl(window.location.href);

    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <nav className={`${s.menu} ${scrolled ? s.slideUp : s.slideDown}`}>
      <BackgroundImage position="absolute" />
      <ul className={s.menu__items}>
        <li className={`${s.logo}`}>
          <a href="/">
            <img
              className={s.logo__img}
              src="/vasculearn-logo.svg"
              alt="Logo Vasculearn network"
            />
          </a>
        </li>
        <div className={s.menu__links}>
          <li className={s.menu__item}>
            <LinkButton
              href="/"
              active={
                !url.includes("patient-questions") &&
                !url.includes("communication-pearls") &&
                url !== ""
              }
              rank="secondary"
              label="Clinician"
              secondLabel="Guide"
            />
          </li>
          <li className={s.menu__item}>
            <LinkButton
              href="/patient-questions"
              active={url.includes("patient-questions")}
              rank="secondary"
              label="Patient"
              secondLabel="Questions"
            />
          </li>
          <li className={s.menu__item}>
            <LinkButton
              href="/communication-pearls"
              active={url.includes("communication-pearls")}
              rank="secondary"
              label="Communication"
              secondLabel="Pearls"
            />
          </li>
        </div>
      </ul>
    </nav>
  );
}
